import m from "mithril"
import { AuthFlow } from "../models/auth.js";
import izitoast from "izitoast";

export var BeforeJoining = {
	view: function() {
		return (
			<section class="pa-modal">
				<div class="tl pt3 f6">
						<h3 class=" f3 ma0 fw4">Before you join</h3>
						<p class="w-100 custom-gray lh-copy ma0 mt1 ">
							Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.
						</p>
						<h3 class="mb0 mt3 fw4 f5">Shop440 Commitment</h3>
						<p class="w-100 custom-gray lh-copy ma0 mt1">
							Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur.
						</p>
						<h3 class="ma0 mt3 fw4 f5">Shop440 Terms of Service</h3>
						<p class="w-100 custom-gray lh-copy mb4 mt1">
							<div class="">
								I also accept{" "}
								<a href="#!" class="custom-green">
									Terms of Service
								</a>,&nbsp;
								<a href="#!" class="custom-green">
									Payments Terms of service
								</a>,{" "}
								<a href="#!" class="custom-green">
									Privacy Policy
								</a>, and&nbsp;
								<a href="#!" class="custom-green">
									Nondiscrimination Policy
								</a>.
							</div>
						</p>
						<br />
						<a
							onclick={()=>{
								izitoast.show({
									title: "SIGNUP",
									message: "Please wait...",
									image: "/assets/img/svg/rings-loader.svg",
									position: "center",
									progressBar	: false,
									timeout: false
								})
								AuthFlow.SignupHandler().then(function(res){
									izitoast.hide(document.querySelector('.iziToast'))
									AuthFlow.Set("welcomeAfterSignup");
								})
								.catch(function(error){
									izitoast.hide(document.querySelector('.iziToast'));
									izitoast.error({
										title: 'Error',
										message: 'Signup was unsuccessful',
										position: 'center' // bottomRight, bottomLeft, topRight, topLeft, topCenter, bottomCenter
									});
									console.log("Signup error: ", error)
								})
							}}
							class="mr2 ph3 pv2 ba b--custom-green bg-custom-green white link bw1 br2 pointer"
						>
							Accept
						</a>
						<a
							class="ph3 pv2 hover-bg-custom-green hover-white ba b--custom-green custom-green no-underline bw1 br2 pointer"
							onclick={()=>{
								// redirect to homepage or close modal if user is on a modal page
								AuthFlow.Set("emailSignup")
							}}
						>
							Decline
						</a>
						<br />
						<br />
					</div>
			</section>
		);
	}
};



