import m from "mithril";
import izitoast from "izitoast";
import { AuthFlow } from "../models/auth.js";

export var AddPhonenumber = {
	oninit:function(vnode){
		AuthFlow.GetCountriesAndPhoneCode()
	},
	state: {confirmbutton: false},
	view: function(vnode) {
		// console.log(AuthFlow.PhoneCodes)
		// console.log(vnode.attrs.countries)
		let options = AuthFlow.PhoneCodes.map(function(country,i){
			if (i===AuthFlow.SelectedPhoneCodeIndex){
				return (
					<option class="pa2" selected >
						{country.name} ({country.callingCodes[0]})
					</option>
				)
			}

			return (
				<option class="pa2"  >
					{country.name} ({country.callingCodes[0]})
				</option>
			)
		})
		return (
			<section class="pa-modal">
				<div class="tc">
					<div class="">
						<p class="f3">Add your phone number</p>
						<p class="f6">
							This is so your customers or merchants can reach you during a
							transaction
						</p>
						<div class="tc">
							<div class="tc dib pa3 ba b--custom-gray bw1 br-100">
								<img class="h3 w3 dib" src="/assets/img/svg/smartphone.svg" />
							</div>
						</div>
						<div class="tc">
							<p class="tc custom-gray">Country</p>
							<div id="countryselect">
								<select
									class="w5 pa3 ba b--black-20 bg-white custom-gray br2"
									style="outline: none"
									id="countryselect"

									onchange={
										m.withAttr("selectedIndex",AuthFlow.SetPhoneCode)}
								>
								{options}
								</select>
							</div>
							<p class="tc custom-gray">Phone Number</p>

							<div class={(AddPhonenumber.state.confirmbutton?" b--black-20 ":" b--red ") + "ba bg-black-10 dib v-mid br2"}>
								<span class="pa2 br2 br--left dib v-mid">
									{AuthFlow.CurrentPhoneCode}
								</span>
								<input
									class="pa2 ba b--transparent br2 br--right"
									style="outline: none"
									placeholder=""
									oninput={m.withAttr("value", function(v){
										if (v.length == 11 && v[0] == 0) {
											v = v.substr(1);
										}
										AuthFlow.User.raw_phone = v;
										if (v.length == 11 || v.length == 10) {

											AuthFlow.VerifyPhonenumberAvailability()
											.then(function(response){
												console.log("Enable the button o: ", response)
												AddPhonenumber.state.confirmbutton = true;
											}).catch(function(err){
												console.log(err)
												console.log("Disable the button o")
												AddPhonenumber.state.confirmbutton = false;
											})

										} else {
											AddPhonenumber.state.confirmbutton = false;
										}
									})}
								/>
							</div>

							<a
								onclick={() => {
									if (!AddPhonenumber.state.confirmbutton) {
										izitoast.error({
											title: 'Error',
											message: 'Invalid Phone Number',
										});
										return;
									}
									// console.log("length: ", AuthFlow.User.raw_phone.length, " value: ", AuthFlow.User.raw_phone);
									if (!AuthFlow.User.raw_phone) {
										izitoast.error({
											title: 'Error',
											message: 'Invalid Phone Number',
										});
										return;
									}
									if (AuthFlow.User.raw_phone.length != 11 && AuthFlow.User.raw_phone.length != 10) {
										izitoast.error({
											title: 'Error',
											message: 'Invalid Phone Number',
										});
										return;
									}
									if (AuthFlow.User.raw_phone.length == 10 && AuthFlow.User.raw_phone[0] == '0') {
										izitoast.error({
											title: 'Error',
											message: 'Invalid Phone Number',
										});
										return;
									}
									AuthFlow.User.phone = "+"+AuthFlow.CurrentPhoneCode + AuthFlow.User.raw_phone
									AuthFlow.VerifyPhonenumber(AuthFlow.User.phone)

									AuthFlow.Set("verifyPhonenumber")

								}}
								class="pointer bg-custom-red br2 db white pa3 br1 ba no-underline w-100 mv4"
							>
								<strong class="dib v-mid ">
									Confirm Phone Number
								</strong>
							</a>


							<p class="custom-gray pointer mb0"
							onclick={()=>{
								AuthFlow.User = {}
								AuthFlow.Set("signup");
							}}>cancel signup</p>
						</div>
					</div>
				</div>
			</section>
		);
	}
};



