import m from 'mithril';

import AuthPage from './containers/authPage.js';
// import OffCanvasMenu from './components/offCanvasMenu.js';
// import AdminShell from './containers/adminShell.js';
// import {MerchantAuth} from './components/auth.js';


var root = document.getElementById('appContainer');

m.route.prefix('/authenticate');


m.route(root, '/', {
  '/':{
      render: function(vnode) {
          return (
            <section class="flex flex-column justify-center   vh-100 items-center fw2 f6">
              <div class="flex ba b--light-gray bw1 w-60-m  w-40-l ">
                <AuthPage/>
              </div>
            </section>

          )
      }
  }
});



