import m from "mithril";
import { AuthFlow } from "../models/auth.js";
import izitoast from "izitoast";

export var VerifyPhonenumber = {
	view: function() {
		return (
			<section class="w-100 pa-modal">
				<div class="tc">
					<div class="">
						<p class="f3">Enter 4-digit code</p>
						<p class="f6">
							We sent an sms to +{AuthFlow.User.phone}. <br />Enter the code in that
							message.
						</p>
						<div class="tc mv2">
							<div class="tc dib pa3 ba b--light-gray bw1 br-100">
								<img class="h3 w3 dib" src="/assets/img/svg/smartphone.svg" />
							</div>
						</div>
						<div class="tc">
							<input
								class="pa2 f3 ba br2 b--custom-gray gray-placeholder w4 mt3 tc"
								style="outline: none"
								placeholder="XXXX"
								type="text"
								oninput={m.withAttr("value", (v)=>{
									if (v==AuthFlow.VerificationCode){
										izitoast.success({
											title: 'Success',
											message: 'Successful Phone Number Verification',
											position: 'topRight'
										});
										AuthFlow.Set("beforeJoining")
									}else{
										if (v.length>3){
											izitoast.error({
												title: 'Error',
												message: 'Incorrect Verification Code',
												position: 'topRight'
											});
										}
									}
								})}
							/>

						<div class="pv2 mt2">
							<span class="tc blue pointer db pa1" onclick={()=>AuthFlow.Set("addPhonenumber")}>Change my number</span>
							<span class="tc blue pointer db " onclick={()=>AuthFlow.VerifyPhonenumber(AuthFlow.User.phone)}>Send code again</span>
							<span class="tc blue pointer db pa1" onclick={()=>console.log("Call the user...")}>Call me instead</span>
						</div>
							<br />
							<br />

							<p class="custom-gray pointer mb0" onclick={()=>{
								AuthFlow.User = {}
								AuthFlow.Set("signup");
							}}>I don't want to continue</p>
						</div>
					</div>
				</div>
			</section>
		);
	}
};



