import m from "mithril";
import { ShowAuth } from "../../frontend/components/auth.js";
import {SVGIcons} from "../../shared/components/svgIcons.js";
import {AuthFlow} from "../models/auth.js";
import izitoast from "izitoast";
import Modal from "../../frontend/models/modal.js";

export var Login = {
	oninit: function() {
		AuthFlow.GetCountriesAndPhoneCode();
		console.log("login")
	},
	view: function() {
		// console.log(AuthFlow.PhoneCodes)
		// console.log(vnode.attrs.countries)
		let options = AuthFlow.PhoneCodes.map(function(country,i){
			if (i===AuthFlow.SelectedPhoneCodeIndex){
				return (
					<option class="pa2 tr" selected >
						{country.cioc}: {country.callingCodes[0]}
					</option>
				)
			}

			return (
				<option class="pa2 tr"  >
					{country.name}: {country.callingCodes[0]}
				</option>
			)
		})
		window.fbAsyncInit = function() {
			FB.init({
			  appId      : process.env.FB_APP_ID,//'772854096255669',
			  cookie     : true,
			  xfbml      : true,
			  version    : 'v2.11'
			});
			  
			FB.AppEvents.logPageView();   
			  
		  };
		
		  (function(d, s, id){
			 var js, fjs = d.getElementsByTagName(s)[0];
			 if (d.getElementById(id)) {return;}
			 js = d.createElement(s); js.id = id;
			 js.src = "https://connect.facebook.net/en_US/sdk.js";
			 fjs.parentNode.insertBefore(js, fjs);
		   }(document, 'script', 'facebook-jssdk'));
		return (
			<section class="w-100 tc pa-modal">
					<div class="">
					<script src="https://apis.google.com/js/api.js" onload="console.log('loaded iam way loaded')"></script>
						<div class="overflow-hidden pa1">
							<a
								class="bg-facebook-blue db mb2 white pa2 br2 ba link w-100 flex flex-row justify-start pointer"
								onclick={() => {
									izitoast.show({
										title: "LOGIN",
										message: "Please wait...",
										image: "/assets/img/svg/rings-loader.svg",
										position: "center",
										progressBar: false,
										overlay: true,
										timeout: false
									})
									console.log("Login with Facebook Login");
									FB.getLoginStatus(function(response) {
										console.log(response);
										if (response.status != "connected") {
											FB.login(function(resp){
												console.log(resp);
												if (resp.status == "connected") {
													// the user accepted the app...
													console.log(resp);
													FB.api("/me?fields=name,email,picture.width(100)", function(r) {
														var toast = document.querySelector('.iziToast');
														izitoast.hide(toast);
														console.log(r);
														// populate the signup object with these details
														AuthFlow.User.name = r.name;
														AuthFlow.User.email = r.email;
														AuthFlow.User.image = r.picture.data.url;
														AuthFlow.User.imageHeight = r.picture.data.height;
														AuthFlow.User.imageWidth = r.picture.data.width;
														AuthFlow.User.migrate = "facebook"
														AuthFlow.User.token = resp.authResponse.accessToken
														console.log("Authflow.User: ", AuthFlow.User);
														AuthFlow.Set("emailSignup");
														// log the user in...
													})
												} else {
													// the user did not accept the app...
													AuthFlow.Set("emailSignup");
													console.log(resp);
												}
											}, {scope: 'public_profile,email'});
											// console.log(response.authResponse);
										} else {
											console.log(response.authResponse);
											FB.api("/me?fields=name,email", function(r) {
												AuthFlow.User.migrate = "facebook";
												AuthFlow.User.email = r.email;
												AuthFlow.User.token = response.authResponse.accessToken;
												AuthFlow.LoginHandler()
												.then(function(){
													var toast = document.querySelector('.iziToast');
													izitoast.hide(toast);
													if (Modal.open) {
														Modal.close()
													}
													console.log(window.location.pathname);
													if (window.location.pathname == "/authenticate/") {
														// redirect to home page...
														window.location.assign("/");
													}
													var event = new Event("loggedin");
													document.dispatchEvent(event);
													m.redraw();
												}).catch(function(){
													var toast = document.querySelector('.iziToast');
													izitoast.hide(toast);
													izitoast.error({
														title: 'Error',
														message: 'Login was unsuccessful',
														position: 'center' // bottomRight, bottomLeft, topRight, topLeft, topCenter, bottomCenter
													});
												})
												console.log(r);
											})
										}
									});
								}}
							>
								<span class="dib v-mid w2 h2" >
									<img src="/assets/img/facebook.png" alt="facebook"/>
									{/* <SVGIcons type="facebook" /> */}
								</span>
								<strong class="dib v-mid flex pa2 justify-center flex-auto" style="flex:1">Log in with facebook</strong>
							</a>
							<a
								class="bg-white db mb2 pa2 br2 ba b--black-20 link flex flex-row justify-start pointer"
								onclick={() => {
									console.log("Login with Google...");
									izitoast.show({
										title: "SIGNUP",
										message: "Please wait...",
										image: "/assets/img/svg/rings-loader.svg",
										position: "center",
										progressBar	: false,
										timeout: false
									})
									gapi.load("client:auth2", function(){
										// Initialize the client with API key and People API, and initialize OAuth with an
										// OAuth 2.0 client ID and scopes (space delimited string) to request access.
										gapi.client.init({
											apiKey: process.env.GAPI_KEY,
											discoveryDocs: [process.env.DICOVERY_DOCS],
											clientId: process.env.GClientID,
											scope: 'profile email'
										}).then(function () {
											// Listen for sign-in state changes.
											console.log("getAuthInstance");
											gapi.auth2.getAuthInstance().isSignedIn.listen(function(isSignedIn) {
												// When signin status changes, this function is called.
												// If the signin status is changed to signedIn, we make an API call.
												if (isSignedIn) {
													// Make an API call to the People API, and print the user's given name.
													/// sign up shhould take place here...
													gapi.client.people.people.get({
														'resourceName': 'people/me',
														// 'requestMask.includeField': 'person.names',
														'personFields': "emailAddresses,names"
													}).then(function(response) {
														console.log(response);
														AuthFlow.User.name = response.result.names[0].givenName + " " + response.result.names[0].familyName;
														AuthFlow.User.email = response.result.emailAddresses[0].value;
														AuthFlow.User.migrate = "google";
														console.log('Hello, ' + response.result.names[0].givenName);
														izitoast.hide(document.querySelector('.iziToast'));
														AuthFlow.Set("emailSignup");
													}, function(reason) {
														console.log(reason);
														console.log('Error: ' + reason.result.error.message);
														izitoast.hide(document.querySelector('.iziToast'));
													});
												} else {
													izitoast.hide(document.querySelector('.iziToast'));
													console.log("Not Signed in...")
												}
											});

											// Handle the initial sign-in state.
											// updateSigninStatus(gapi.auth2.getAuthInstance().isSignedIn.get());
											if (gapi.auth2.getAuthInstance().isSignedIn.get()) {
												// DO LOGIN
												// get email and verify a token...
												// Make an API call to the People API, and print the user's given name.
												console.log("make api call");
												gapi.client.people.people.get({
													'resourceName': 'people/me',
													// 'requestMask.includeField': 'person.names'
													'personFields': "emailAddresses,names"
												}).then(function(response) {
													AuthFlow.User.migrate = "google";
													AuthFlow.User.token = gapi.auth2.getAuthInstance().currentUser.get().getAuthResponse().id_token;
													AuthFlow.User.name = response.result.names[0].givenName + " " + response.result.names[0].familyName;
													AuthFlow.User.email = response.result.emailAddresses[0].value;
													AuthFlow.LoginHandler()
													.then(function(){
														izitoast.hide(document.querySelector('.iziToast'));
														if (Modal.open) {
															Modal.close()
														}
														console.log(window.location.pathname);
														if (window.location.pathname == "/authenticate/") {
															// redirect to home page...
															// i really dont remember why i did this but let it be here incase: window.location.assign("/");
															window.location.assign("/merchants");
														}
														var event = new Event("loggedin");
														document.dispatchEvent(event);
														m.redraw();
													}).catch(function(){
														izitoast.hide(document.querySelector('.iziToast'));
														izitoast.error({
															title: 'Error',
															message: 'Login was unsuccessful',
															position: 'center' // bottomRight, bottomLeft, topRight, topLeft, topCenter, bottomCenter
														});
													})
													console.log(r);
													console.log('Hello, ' + response.result.names[0].displayName, AuthFlow.User.email);
													izitoast.hide(document.querySelector('.iziToast'));
												}, function(reason) {
													console.log(reason);
													console.log('Error: ' + reason.result.error.message);
													izitoast.hide(document.querySelector('.iziToast'));
												});
											} else {
												
												gapi.auth2.getAuthInstance().signIn();
											}
										});
									})
									// GoogleAuth = gapi.auth2.GoogleAuth
								}}
							>
								<span class="dib v-mid w2 h2">
									<SVGIcons type="google" />
								</span>
								<strong class="dib v-mid pa2 justify-center flex flex-auto" >Log in with Google</strong>
							</a>
							<span class="or relative pa3 tc">
								<span class="gray pv2">or</span>
							</span>
							<div class="">
							<div id="countryselect" class="w-30 dib ">
								<select
									class="w-100 pa3 ba b--black-20 bg-white custom-gray br2"
									style="outline: none"
									id="countryselect"
									onchange={m.withAttr("selectedIndex",AuthFlow.SetPhoneCode)}
								>
								{options}
								</select>
							</div><input
								class="pa3 mv2 ba b--black-20 br2 w-70 dib f5 gray-placeholder"
								placeholder="8161111111"
								type="tel"
								name="phones"
								value={AuthFlow.User.raw_phone}
								oninput={m.withAttr("value", (val)=>{
									AuthFlow.User.raw_phone = val;
								})}
							/>
					</div>
							<input
								class="pa3 mv2 ba b--black-20 br2 w-100 db f5 gray-placeholder"
								placeholder="Password"
								type="password"
								name="password"
								value={AuthFlow.User.password}
								oninput={m.withAttr("value", (val)=>AuthFlow.User.password=val)}

							/>
						<div class="cf pv1">
								<span class="fl">
									<input type="checkbox" name="remember" /> Remember me
								</span>
								<span class="fr">
									<a href="#!" class="no-underline blue">
										Forgot password?
									</a>
								</span>
							</div>
							<a
								class="pointer bg-custom-red br2 db mv2 white pa3 br1 ba no-underline w-100"
								onclick={()=>{
									var raw_phone = AuthFlow.User.raw_phone || "";
									if (raw_phone.length == 11 && raw_phone[0] == 0) {
										raw_phone = raw_phone.substr(1);
									} else if (raw_phone.length < 10 || raw_phone.length > 11) {
										izitoast.error({
											title: "Error",
											message: "Invalid Phone number",
											position: "topRight"
										})
										return
									}
									// AuthFlow.User.phone = val;
									AuthFlow.User.phone = "+"+AuthFlow.CurrentPhoneCode + raw_phone;
									console.log("Submitted phone number: ", AuthFlow.User.phone)
									izitoast.show({
										title: "LOGIN",
										message: "Please wait...",
										image: "/assets/img/svg/rings-loader.svg",
										position: "center",
										progressBar	: false
									})
									AuthFlow.LoginHandler()
									.then(function(){
										var toast = document.querySelector('.iziToast');
										izitoast.hide(toast);
										if (Modal.open) {
											Modal.close()
										}
										console.log(window.location.pathname);
										if (window.location.pathname == "/authenticate/") {
											// redirect to home page...
											window.location.assign("/");
										}
										var event = new Event("loggedin");
										document.dispatchEvent(event);
										m.redraw();
									}).catch(function(){
										var toast = document.querySelector('.iziToast');
										izitoast.hide(toast)
										izitoast.error({
											title: 'Error',
											message: 'Login was unsuccessful',
											position: 'center' // bottomRight, bottomLeft, topRight, topLeft, topCenter, bottomCenter
										});
									});
								}}
							>
								<strong class="dib v-mid pa2">Log in</strong>
							</a>
							<p class="w-100 bb b--light-gray" />
							<div class="cf">
								<span class="custom-gray fl pv2">
									Don't have an account?
								</span>{" "}
								<a
									class="dib fr pa2 custom-red no-underline ba br2 b--custom-red pointer"
									onclick={()=>AuthFlow.Set("signup")}
								>
									Sign up
								</a>
							</div>
						</div>
					</div>
			</section>
		);
	}
};



