import m from "mithril";
import izitoast from "izitoast";
import { AuthFlow } from "../models/auth.js";

export var EmailSignup = {
	view: function() {
		return (
			<section class="pa-modal">
				<div class="overflow-hidden tc ">
				<p class="db">
					Signup with &nbsp;
					<a class="blue" onclick={function(){
						FB.getLoginStatus(function(response) {
							statusChangeCallback(response);
						});
					}}>Facebook</a>&nbsp; or &nbsp;<a class="red">Google</a>
				</p>
				<span class="or relative pa3 tc">
					<span class="gray pv2">or</span>
				</span>
				<div class="">
					<input
						class="pa3 mv2 ba b--black-20 br2 db f5 w-100 black-60 gray-placeholder"
						placeholder="Name"
						type="text"
						name="name"
						value={AuthFlow.User.name}
						oninput={m.withAttr("value", (val)=>AuthFlow.User.name=val)}
					/>
					<input
						class="pa3 mv2 ba b--black-20 br2 db f5 w-100 black-60 gray-placeholder"
						placeholder="Email address"
						type="email"
						name="email"
						value={AuthFlow.User.email}
						oninput={m.withAttr("value", (val)=>AuthFlow.User.email=val)}
					/>
					<input
						class="pa3 mv2 ba b--black-20 br2 db f5 w-100 black-60 gray-placeholder"
						placeholder="Password"
						type="password"
						name="password"
						value={AuthFlow.User.password}
						oninput={m.withAttr("value", (val)=>AuthFlow.User.password=val)}
					/>
					<input
						class="pa3 mv2 ba b--black-20 br2 db f5 w-100 black-60 gray-placeholder"
						placeholder="Repeat Password"
						type="password"
						name="repeat_password"
						oninput={m.withAttr("value", (val)=>AuthFlow.User.repeat_password=val)}

					/>
					{/* { TODO:: Password strenght } */}

				</div>
				<div class="tl flex">
					<input class="dib flex vmid mt2 mr2 " type="checkbox" name="checked"
						value={AuthFlow.User.consent}
						oninput={m.withAttr("value", (val)=>AuthFlow.User.consent=val)}
					/>
					<span class="lh-copy dib flex-auto vmid">
						&nbsp;
						Id like to receive marketing and policy communications from
						Shop440.
					</span>
				</div>
				<button
					onclick={() => {
						if (!AuthFlow.User.email) {
							izitoast.error({
								title: 'Error',
								message: 'Email cannot be empty.',
							});
							return;
						}
						if (!AuthFlow.User.password) {
							izitoast.error({
								title: 'Error',
								message: 'Password is empty.',
							});
							return;
						}
						if (AuthFlow.User.repeat_password != AuthFlow.User.password) {
							izitoast.error({
								title: 'Error',
								message: 'Passwords do not match.',
							});
							return;
						}
						AuthFlow.Set("addPhonenumber");
					}}
					class="bg-custom-red mv2 white pa3 br2 ba no-underline w-100 pointer"
				>
					<strong class="dib v-mid">Sign up</strong>
					{/* TODO:: Loader on signup */}
				</button>
				<p class="w-100 bb b--light-gray" />
				<span class="custom-gray ">
					Already have a shop440 account?{" "}
					<a class="blue pointer" onclick={() => AuthFlow.Set("login")}>
						Log in
					</a>
				</span>
			</div>
			</section>
		);
	}
};



