import m from "mithril";
import { AuthFlow } from "../models/auth.js";
import {SVGIcons} from "../../shared/components/svgIcons.js";

export var WelcomeAfterSignup = {
	view: function() {
		return (
			<div class="tc fw2">
				<div
					class=""
					style="background: white url('/assets/img/happy.jpeg') center no-repeat; background-size: cover"
				>
					<div class="bg-black-80 ph3 pt6 pb2">
						<SVGIcons type="logo-white" class="h3" />
						<h3 class="white f3 fw2">Welcome to shop440</h3>
						<p class="w5 dib white lh-copy dib" fw2>
							Shop440 merchants and customers are real people with real goods.
							That's why you'll have to confirm a few, quick details to activate
							your account.
						</p>
						<button
							onclick={()=>AuthFlow.Set("uploadProfilePic")}
							class="bg-custom-red w5 pointer dib mv2 white pa2 br2 ba b--custom-red no-underline "
						>
							<strong class="dib v-mid fw2">Next - 1 step left</strong>
						</button>
					</div>
				</div>
			</div>
		);
	}
};



