import m from "mithril";
import { AuthFlow } from "../models/auth.js";
import { LoginErr } from "../components/login.js";
import { Login } from "../components/login.js";
import { ChooseSignupStyle } from "../components/chooseSignupStyle.js";
import { UploadProfilePic } from "../components/uploadProfilePicture.js";
import {VerifyPhonenumber} from "../components/verifyPhonenumber.js";
import {OnSignupFlowCompletion} from "../components/onSignupFlowCompletion.js";
import {WelcomeAfterSignup} from "../components/welcomeAfterSignup.js";
import {EmailSignup} from "../components/emailSignup.js";
import {BeforeJoining} from "../components/beforeJoining.js";
import {AddPhonenumber} from "../components/addPhonenumber.js";


let components = {
	login: Login,
	signup: ChooseSignupStyle,
	uploadProfilePic: UploadProfilePic,
	onSignupFlowCompletion: OnSignupFlowCompletion,
	welcomeAfterSignup: WelcomeAfterSignup,
	emailSignup: EmailSignup,
	beforeJoining: BeforeJoining,
	addPhonenumber: AddPhonenumber,
	verifyPhonenumber: VerifyPhonenumber,
};

var AuthPage = {
	view: () => {
		console.log(AuthFlow.Get())
		return (
			<section class="w-100">
				{m(components[AuthFlow.CurrentPage])}
			</section>
		)
	},
};

export default AuthPage;



