import m from "mithril";
import izitoast from 'izitoast';
import localforage from "localforage";

export var AuthFlow = {
	CurrentPage: "login",//uploadProfilePic
	User:{},
	PhoneCodes:[],
	CurrentPhoneCode:"234",
	SelectedPhoneCodeIndex:162,
	VerificationCode:"",
	Set: function(page){
		console.log(page)
		console.log(AuthFlow.User)
		AuthFlow.CurrentPage = page
		m.redraw()
	},
	Get: function(){
		return AuthFlow.CurrentPage
	},
	GetCountriesAndPhoneCode:function(){
		return m.request({
				method: "GET",
				url: "https://restcountries.eu/rest/v2/all?fields=name;callingCodes;cioc"
			})
			.then(function(resp){
				console.log(resp)
				AuthFlow.PhoneCodes = resp
				return AuthFlow.PhoneCodes
				// m.redraw()
			})
			.catch(function(error) {
				console.error(error);
			});

	},
	GetPhoneCodes(){
		return AuthFlow.PhoneCodes
	},
	SetPhoneCode(index){
		AuthFlow.CurrentPhoneCode = AuthFlow.PhoneCodes[index].callingCodes[0]
		AuthFlow.SelectedPhoneCodeIndex = index
	},
	VerifyPhonenumber(phonenumber){
		return m.request({
				method: "GET",
				url: `/api/users/verifyphone?p=${phonenumber}`
			})
			.then(function(resp){
				console.log(resp)
				AuthFlow.VerificationCode = resp.code
			})
			.catch(function(error) {
				console.error(error);
			});
	},
	VerifyPhonenumberAvailability(){
		AuthFlow.User.phone = "+"+AuthFlow.CurrentPhoneCode + AuthFlow.User.raw_phone
		// AuthFlow.User.phone = phonenumber;
		return m.request({
			method: "POST",
			url: "/api/users/checkphonenumber",
			data: AuthFlow.User
		}).then(function(response){
			console.log("Verify Phone Number availablity response: ", response);
			return response;
		}).catch(function(err){
			console.log("Error: ", err);
			throw err;
			// return err;
		})
	},
	SignupHandler(){
		return m.request({
				method: "POST",
				url: `/api/users/signup`,
				data: AuthFlow.User,
			})
			.then(function(resp){
				localforage.setItem("user",resp.user).then(()=>{
					window.sendinblue.identify(AuthFlow.User.email, {
					  'FIRSTNAME':`${AuthFlow.User.name.trim().split(" ")[0]}`,
					  'LASTNAME' : `${AuthFlow.User.name.trim().split(" ")[1]}`,
						'SMS': `${AuthFlow.User.phone}`,
						'MERCHANT':false,
					});
					izitoast.success({
						title: 'Success',
						message: 'Successful Signup',
						position: 'topRight'
					});
					AuthFlow.Set("welcomeAfterSignup")
				})
			})
			// .catch(function(error) {
			// 	console.log("Signup Error: ", error)
			// 	iziToast.error({
			// 		theme: 'dark',
			// 		title: 'Error',
			// 		message: 'Signup was unsuccessful',
			// 		position: 'center', // bottomRight, bottomLeft, topRight, topLeft, topCenter, bottomCenter
			// 		progressBarColor: 'rgb(0, 255, 184)',
			// 	});
			// 	throw err
			// });
	},
	LoginHandler(){
			return m.request({
					method: "POST",
					url: `/api/users/login`,
					data: AuthFlow.User,
				})
				.then(function(resp){
					// console.log(user)
					let user = resp.user;
					if (user) {
						window.sendinblue.identify(user.email, {
						  'FIRSTNAME':`${user.name.trim().split(" ")[0]}`,
						  'LASTNAME' : `${user.name.trim().split(" ")[1]}`,
							'SMS': `${user.phone}`,
						});

						localforage.setItem("user",user).then((c)=>{
							izitoast.success({
								title: 'Success',
								message: 'Logged in successfully',
								position: 'topRight'
							});

							location.assign("/merchants/");
						})
					}else{
						izitoast.error({
							title: 'Error',
							message: 'Login was unsuccessful',
							position: 'topRight'
						});
					}
				})
				.catch(function(error) {
					izitoast.error({
						theme: 'dark',
						icon: 'icon-person',
						title: 'Error',
						message: 'Login was unsuccessful',
						position: 'center', // bottomRight, bottomLeft, topRight, topLeft, topCenter, bottomCenter
						progressBarColor: 'rgb(0, 255, 184)',
					});
					console.log("LoginError: ", error)
					throw error;
				});
		}
}



