import m from "mithril";
import localforage from "localforage";
import { getCookie, deleteCookie } from "../../util/cookie.js";
import { isEmptyObject } from "../../util/utils.js";
// import { UserAuth } from "../components/auth";

export var UserModel = {
	NewUser: {},
	User: {},
	Orders: [],
	GetUserfromStorage: function() {
		console.log("user : ", UserModel.User);
		// if (getCookie("X-USER-TOKEN") !== "") {
		// 	console.log("No user, lets look for a user");
			return localforage.getItem("user").then(function(user) {
				console.log("Got User");
				if (!isEmptyObject(user)) {
					UserModel.User = user;
					console.log(UserModel.User);
					m.redraw();
					return;
				}
				UserModel.User = {};
				m.redraw();
			}).catch(function(error) {
				return error
			});
		// } else {
		// 	return new Promise((resolve, reject) => {
		// 		reject("Not logged in");
		// 	});
		// }
	},
	GetOrders: function() {
		return m.request({
			method: "GET",
			url: "/api/orders/user/"+UserModel.User.phone
		}).then(function(resp){
			console.log("ORDERS: ", resp);
			UserModel.Orders = resp;
			m.redraw();
		}).catch(function(err){
			console.log(err)
		})
	}
};



