import m from "mithril"
import {SVGIcons} from "../../shared/components/svgIcons.js";
import {AuthFlow} from "../models/auth.js";
import {IMAGESERVER} from "../../shared/constants.js";

export var UploadProfilePic = {
	state: {
		uploading: false,
		uploadedImage: {},
		uploaded: false
	},
	UploadFile:function(e){
		return new Promise(function(resolve, reject){
			var files = e.target.files || e.dataTransfer.files;

			let file = files[0];
			var reader = new FileReader();
			reader.onload = (() => {
				return e => {
					console.log(IMAGESERVER);
					m.request({
						method:"POST",
						url:`${IMAGESERVER}/upload`,
						data: {
							Type:"image",
							Content:e.target.result,
						}
					}).then(function(resp){
						AuthFlow.User.image = resp.url
						resolve(resp);
					}).catch(function(err){
						reject(err);
					})
				};
			})();
			reader.readAsDataURL(file);
		})
	},
	view: function() {
		return (
			<div class="tc pa-modal">
				<h3>Add your profile photo</h3>
				<p class="w-100 dib">
					Put a face to your name! We'll add this to your profile, and share it
					with merchants and customers
				</p>
				{UploadProfilePic.state.uploading?
				<SVGIcons type="loader"/>
				:<span class="w4 h4 tc overflow-hidden dib"><img class={AuthFlow.User.migrate == "facebook"?"dib":" dib"}
					style={AuthFlow.User.migrate == "facebook"?
						"width: "+AuthFlow.User.imageWidth+"; height: "+AuthFlow.User.imageHeight:""}
					alt=""
					src={AuthFlow.User.image?AuthFlow.User.image:"/assets/img/avatar.jpg"}
				/></span>}
				{AuthFlow.User.migrate != "facebook"?
				<a
					class="bg-facebook-blue db  mb2 mt2 white pa3 br2 ba link w-100 flex flex-row justify-center "
				>
					<span class="dib v-mid pr2  " >
						<SVGIcons type="facebook" />
					</span>
					<strong class="dib v-mid " >Use Facebook Photo</strong>
				</a>:""}
				<label for="profile_picture"
					class="bg-white w-100 dib mv2 ba b--custom-gray custom-gray pa3 br2 no-underline pointer"
				>
					<span class="dib v-mid pr2">
						<img class="dib v-mid w1 h1" src="/assets/img/svg/upload.svg" />
					</span>
					<strong class="dib v-mid">
						Upload Photo
						<input name="profile_picture" id="profile_picture" type="file" class="dn"
						onchange={function(e){
							UploadProfilePic.state.uploading = true;

							UploadProfilePic.UploadFile(e)
								.then(function(resp){
									console.log("upload image response: ", resp);
									// update the user's image to the server
									m.request({
										method:"PUT",
										url:"/api/users/updateimage",
										data: AuthFlow.User
									}).then(function(resp){
										UploadProfilePic.state.uploading = false;
										UploadProfilePic.state.uploaded = true;
									}).catch(function(err){
										UploadProfilePic.state.uploading = false;
										console.log("Update image error: ", err);
									})
								})
								.catch(function(err){
									UploadProfilePic.state.uploading = false;
								})
						}}/>
					</strong>
				</label>
				{AuthFlow.User.migrate == "facebook" || UploadProfilePic.state.uploaded?
				<p
					class="green pointer mb0 mt3"
					onclick={()=>AuthFlow.Set("onSignupFlowCompletion")}
				>
					NEXT &raquo;
				</p>:
				<p
					class="custom-gray pointer mb0 mt3"
					onclick={()=>AuthFlow.Set("onSignupFlowCompletion")}
				>
					I'll do this later
				</p>}
			</div>
		);
	}
};



